<template lang="pug">
	.viewer.container-control.my-4
		.viewer-header.container-control.d-flex.align-items-center.mb-4.text-capitalize
				.viewer-comic-name(@click="$emit('closeViewer')") {{ book.name }}
				.viewer-select-control
					select#episodes-select.custom-select(v-model="currentEpIndex", @change="changeEpisode")
						option(v-for="(episode, index) in book.episodes", :value="index") episode {{ episode.id }}
					select#pages-select.custom-select(v-model="currentPage", @change="changePage")
						option(v-for="page in book.pages[currentEpIndex].page", :value="page") page {{page < 10 ? '0' : ''}}{{page}}
				.viewer-mode-switch.d-flex.align-items-center
					i.fas.fa-sun(:class="{'switch-off': nightMode}")
					input#nightMode-switch.d-none(type="checkbox", v-model="nightMode", :true-value="true", :false-value="false", @change="$emit('switchNightMode', nightMode)")
					label.mx-2.mb-0(for="nightMode-switch")
						span.slider.d-inline-block.position-relative
					i.far.fa-moon(:class="{'switch-off': !nightMode}")
		//- .viewer-body.d-flex(:class="{'viewer-fluid': viewerFluid}")
		.viewer-body.d-flex
			span.viewer-fluid-close(@click="viewerFluid = false") X
			.pre-page.btn-page(:class="{'no-content': firstPage}", @click="changePage('decrease')", @touchstart= "touchDown('.pre-page')", @touchend= "touchUp('.pre-page')")
				i.fas.fa-chevron-left.pl-4
			//- .viewer-content(@click="viewerFluid = true")
			.viewer-content
				img.img-fluid(:src="currentPageUrl")
			.next-page.btn-page(:class="{'no-content': lastPage}", @click="changePage('increase')", @touchstart= "touchDown('.next-page')", @touchend= "touchUp('.next-page')")
				i.fas.fa-chevron-right.pl-4
		.viewer-footer.container-control
			.viewer-page-bar.d-flex
				//- span.pre-page.btn-page.position-relative(@click="changePage('decrease')")
				//- 	i.fas.fa-caret-left.icon-control
				#js-scroll-bar.scroll-bar.position-relative(@mousedown="startDrag", @touchstart="startDrag",  @click="handleSelfClick")
					#js-scroll-pointer.scroll-pointer.position-relative
						i.fas.fa-circle.icon-control
				//- span.next-page.btn-page.position-relative(@click="changePage('increase')")
				//- 	i.fas.fa-caret-right.icon-control
			.preview-display.position-relative
				span.pre-episode.btn-episode(:class="{'no-content': firstEpisode}", @click="changeEpisode('decrease')", @touchstart= "touchDown('.pre-episode')", @touchend= "touchUp('.pre-episode')")
					i.fas.fa-angle-double-left.icon-control
				.preview-content.position-relative
					#js-preview-images.preview-images.d-flex
						div(v-for="page in book.pages[currentEpIndex].page", :class="{active: page === currentPage}", @click="clickPreviewWindow(page)")
							span.page-number {{page < 10 ? '0' : ''}}{{page}}
							img.img-fluid(:src="`${baseUrl}${epPageData.filename}${ep}${page < 10 ? '00' : page < 100 ? '0' : ''}${page}${epPageData.suffix}.jpg`")
				span.next-episode.btn-episode(:class="{active: lastPage, 'no-content': lastEpisode}", @click="changeEpisode('increase')", @touchstart= "touchDown('.next-episode')", @touchend= "touchUp('.next-episode')")
					i.fas.fa-angle-double-right.icon-control
		//- .viewer-fluid-bg(:class="{open: viewerFluid}")
		.viewer-fluid-bg

</template>

<script>
	import $ from 'jquery'

	export default {
		name: 'viewer',
		props: ['book', 'episodeIndex', 'page'],
		data() {
			return {
				nightMode: false,
				viewerFluid: false,
				dragging: false,
				currentEpIndex: "",
				currentPage: "",
				firstPage: true,
				lastPage: false,
				firstEpisode: true,
				lastEpisode: false,
				baseUrl: "",
			}
		},
		methods: {
			changePage(mode) {
				const vm = this;
				if (mode === 'increase') {
					vm.currentPage + 1 <= vm.epPageData.page ? vm.currentPage++ : false;
					vm.firstPage = vm.currentPage > 1 ?  false : true;
				} else if (mode === 'decrease') {
					vm.currentPage - 1 >= 1 ? vm.currentPage-- : false;
				}
				vm.movePreviewBar();
				vm.movePrevieWindow();
			},
			changeEpisode(mode) {
				const vm = this;
				if (mode === 'increase') {
					vm.currentEpIndex + 1 <= vm.book.episodes.length - 1 ? vm.currentEpIndex++ : false;
				} else if (mode === 'decrease') {
					vm.currentEpIndex - 1 >= 0 ? vm.currentEpIndex-- : false;
				}
				vm.currentPage = 1;
				vm.movePreviewBar();
				vm.movePrevieWindow();
			},
			isLastPage() {
				const vm = this;
				if (vm.lastEpisode) {
					!vm.lastPage ? false : vm.lastPage = false;
				} else {
					vm.currentPage === vm.epPageData.page ? vm.lastPage = true : vm.lastPage = false;
				}
			},
			isFirstEpisode() {
				const vm = this;
				vm.currentEpIndex === 0 ? vm.firstEpisode = true : vm.firstEpisode = false;
			},
			isLastEpisode() {
				const vm = this;
				vm.currentEpIndex === vm.book.episodes.length - 1 ? vm.lastEpisode = true : vm.lastEpisode = false;
			},
			handleSelfClick(event) {
				const vm = this;
				if("ontouchstart" in document) {
					vm.touchScrollBar(event);
				} else {
					vm.clickScrollBar(event);
				}
			},
			movePrevieWindow() {
				const vm = this;
				const $previewWindow = $('#js-preview-images');
				const space = 107;
				const moveFromPage = 4
				let totalPage = vm.epPageData.page;
				if (vm.currentPage > totalPage - moveFromPage) {
					let moveSpace = (totalPage - moveFromPage - 1) * space;
					$previewWindow.css({
						transform: `translateX(-${moveSpace}px)`
					})
				} else if (vm.currentPage >= moveFromPage) {
					let moveSpace = (vm.currentPage - moveFromPage + 1) * space;
					$previewWindow.css({
						transform: `translateX(-${moveSpace}px)`
					})
				} else if (vm.currentPage < moveFromPage) {
					$previewWindow.css({
						transform: `translateX(0px)`
					});
				}
			},
			movePreviewBar(wheelSpace) {
				const vm = this;
				const $scrollBar = $('#js-scroll-bar')
				const $scrollPointer = $('#js-scroll-pointer');
				const barBorder = 4;
				const pointerWidth = $scrollPointer.width();
				const pointerXMargin = 0;
				const barWidth = $scrollBar.width() - (pointerXMargin * 2) - pointerWidth;
				const barXStart = $scrollBar.offset().left + barBorder + pointerXMargin + (pointerWidth / 2);
				const barXEnd = barXStart + barWidth;
				const totalPage = vm.epPageData.page;
				const eachPageWidth = Math.floor(barWidth / (totalPage - 1));

				if (!wheelSpace) {
					if (vm.currentPage === 1) {
						var moveSpace = 0;
					} else if (vm.currentPage === totalPage) {
						var moveSpace = (vm.currentPage - 1) * eachPageWidth < barWidth ? barWidth : (vm.currentPage - 1) * eachPageWidth;
					} else {
						var moveSpace = (vm.currentPage - 1) * eachPageWidth;
					}
				} else {
					if (wheelSpace <= 0) {
						vm.currentPage = 1;
						var moveSpace = 0;
					} else if (wheelSpace >= barWidth) {
						vm.currentPage = totalPage;
						var moveSpace = (totalPage - 1) * eachPageWidth < barWidth ? barWidth : (totalPage - 1) * eachPageWidth;
					} else {
						var moveSpace;
						let moveToPage = parseInt(wheelSpace / eachPageWidth);
						vm.currentPage = moveToPage < 1 ? 1 : moveToPage;
						vm.currentPage === 1 ? moveSpace = 0 : moveSpace = ((moveToPage - 1) * eachPageWidth)
					}
				}
				$scrollPointer.css({
					transform: `translate(${moveSpace}px, -50%)`
				});
				vm.isLastPage();
				vm.isLastEpisode();
				vm.isFirstEpisode();
				vm.firstPage = vm.currentPage > 1 ?  false : true;

			},
			clickScrollBar() {
				const vm = this;
				const $scrollBar = $('#js-scroll-bar');
				const barX = $scrollBar.offset().left;
				const wheelSpace = event.clientX - barX;
				vm.movePreviewBar(wheelSpace);
				vm.movePrevieWindow();
			},
			clickPreviewWindow(page) {
				const vm = this;
				vm.currentPage = page;
				vm.movePreviewBar();
			},
			touchScrollBar(event) {
				var touch;
				const vm = this;
				const $scrollBar = $('#js-scroll-bar');
				const barX = $scrollBar.offset().left;
				if (event.touches) {
					touch = event.touches[0];
				} else {
					touch = event;
				}
				const wheelSpace = touch.clientX - barX;
				vm.movePreviewBar(wheelSpace);
				vm.movePrevieWindow();
			},
			startDrag() {
				const vm = this;
				vm.dragging = true;
			},
			onWheel(event) {
				const vm = this;
				if (vm.dragging && "ontouchstart" in document) {
					vm.touchScrollBar(event);
				} else if (vm.dragging){
					vm.clickScrollBar(event);
				}
			},
			stopDrag() {
				const vm = this;
				vm.dragging = false;
			},
			touchDown(eleName) {
				const vm = this;
				const $ele = $(eleName);
				$ele.removeClass('btn-unhover');
				$ele.addClass('btn-hover');
				console.log("touchDown")
			},
			touchUp(eleName) {
				const vm = this;
				const $ele = $(eleName);
				$ele.removeClass('btn-hover');
				$ele.addClass('btn-unhover');
				console.log("touchUP")
			}
		},
		computed: {
			ep() {
				const vm = this;
				let ep = vm.book.episodes[vm.currentEpIndex].id;
				if (typeof(ep) === 'string') {
					ep = ep.replace('ex', '');
				}
				return `${ep}_`
			},
			epPageData() {
				const vm = this;
				return vm.book.pages[vm.currentEpIndex];
			},
			currentPageUrl() {
				const vm = this;
				const pagePrefix = vm.currentPage < 10 ? '00' : vm.currentPage < 100 ? '0' : '';
				return `${vm.baseUrl}${vm.epPageData.filename}${vm.ep}${pagePrefix}${vm.currentPage}${vm.epPageData.suffix}.jpg`
			}
		},
		created() {
			const vm = this;
			vm.currentEpIndex = vm.episodeIndex;
			vm.currentPage = vm.page;
			vm.baseUrl = vm.book.source;
			vm.isLastEpisode();
		},
		mounted() {
			const vm = this;
			window.addEventListener('mouseup', vm.stopDrag);
			window.addEventListener('mousemove', vm.onWheel);
			window.addEventListener('touchend', vm.stopDrag);
			window.addEventListener('touchmove', vm.onWheel);
		}
	}
</script>



